#nav {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  flex-direction: column;
  top: 0;
  padding: 5px;
  transition: 0.5s;
  border-radius: 50px;
  overflow: hidden;
  box-shadow: 0 8px 15px rgba(0,0,0,.2);
  background-color: rgb(255, 255, 255 ,.4);
  z-index: 2;
}

.toggleBox {
  margin: 0;
  padding: 0;
  height: 0;
  display: flex;
  flex-direction: column;
  transition: 0.7s;
}

#nav input:checked ~ .toggleBox {
  height: 400px;
}

.toggleButton {
  width: 100px;
  margin: 0 10px;
  transition: 0.5s;
}

.toggleButton input{
  width: 0px;
  height: 0px;
  margin: 0;
  visibility: hidden;
}

#nav>input {
  width: 110px;
  height: 40px;
  cursor: pointer;
  opacity: 0;
}

#nav span {
  position: fixed;
  top: 30px;
  width: 30px;
  height: 4px;
  border-radius: 50px;
  background-color: #666;
  pointer-events: none;
  transition: 0.5s;
}

#nav input:checked ~ span {
  background-color: #f974a1;
}

#nav span:nth-child(2) {
  transform: translateY(-8px);
}

#nav input:checked ~ span:nth-child(2) {
  transform: translateY(0) rotate(-45deg);
}
#nav span:nth-child(3) {
  transform: translateY(8px);
}

#nav input:checked ~ span:nth-child(3) {
  transform: translateY(0px) rotate(45deg);
}

.toggleText{
  color: rgb(0, 0, 0);
}

input:checked~.toggleSwitch .toggleText{
  color: #e40077;
}