@import url('https://cdn.jsdelivr.net/npm/galmuri@latest/dist/galmuri.css');
@import url('card.css');
@import url('menu.css');

::-webkit-scrollbar {
  display: none;
  }
body{
  height: 100%;
  color: #fff;
  background-color: #F5E2CB;
  font-family:'Galmuri11';
  overflow: hidden;
  -webkit-user-select:none;
  -moz-user-select:none;
  -ms-user-select:none;
  user-select:none
}
body * { touch-action: none; }
.App {
  text-align: center;
}
#title{
  text-shadow: 2px 5px 5px #000;
}
#gacha{
  width: 300px;
  height: 70px;
  position: relative;
  transform: translateX(-50%);
  left: 50%;
  margin-top: 60px;
  margin-bottom: 20px;
}
#inner_button{
  margin: 2px;
  width: calc(100% - 4px);
  height: calc(100% - 4px);
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
#inner_button:after{
  content:"";
  height:100%;
  width:100%;
  padding:4px;
  position: absolute;
  bottom:-15px;
  left:-4px;
  z-index:-1;
  background-color:#2B1800;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 5px;
}
#inner_button p{
  color:white;
  font-weight:bold;
  font-size:36px;
  text-align: center;
  background-color:#e60012;
  display:block;
  position:relative;
  padding:10px 40px;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  text-shadow: 0px 1px 0px #000;
  filter: dropshadow(color=#000, offx=0px, offy=1px);
  
  -webkit-box-shadow:inset 0 1px 0 #ffc4c4, 0 10px 0 #910000;
  -moz-box-shadow:inset 0 1px 0 #ffc4c4, 0 10px 0 #910000;
  box-shadow:inset 0 1px 0 #ffc4c4, 0 10px 0 #910000;
  
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 5px;
}
#inner_button p:active{
  top:10px;
  background-color:#c00010;
  
  -webkit-box-shadow:inset 0 1px 0 #ffc4c4, inset 0 -3px 0 #910000;
  -moz-box-shadow:inset 0 1px 0 #ffc4c4, inset 0 -3pxpx 0 #910000;
  box-shadow:inset 0 1px 0 #ffc4c4, inset 0 -3px 0 #910000;
}
footer{
  width: 100%;
  position: absolute;
  bottom: 0;
  overflow: hidden;
  z-index: -2;
}
footer #line{
  height: 50px;
  background-image: url("../public/img/bottom-line-img.png");
}
header #line{
  height: 50px;
  background-image: url("../public/img/top-line-img.png");
}