  #card{
    height: 490px;
  }
  #frame{
    transform: translateX(-50%);
    position: absolute;
    left: 50%;
    width: 330px;
    height: 490px;
    z-index: 1;
    background-image: url("../public/img/trailer_flame.png");
    background-size: 100% 100%;
  }
  #content{
    transform: translateX(-50%);
    position: absolute;
    left: 50%;
    margin-top: 5px;
    width: 320px;
    height: 480px;
    display: flex;
    flex-direction: column;
    background: linear-gradient(303deg,rgba(0,0,0,0.8) 0%, rgba(255,255,255,0.3) 50%,rgba(0,0,0,0.8) 100%, transparent);
    text-shadow: 1px 1px 1px #161816;
  }
  #species{
    text-align: left;
    margin-left: 30px;
  }
  #infoBox p{
    font-size: 15px;
    margin: 0;
  }
  #infoBox h1{
    margin-top: 10px;
    height: 40px;
    font-size: 25px;
  }
  #attribute{
    width: 320px;
    height: 100px;
    margin-left: 10px;
    margin-top: 10px;
    text-align: left;
    display: flex;
    flex-wrap: wrap;
  }
  #attribute img{
    width: 30px;
    height: 30px;
  }
  #attribute p{
    width: 30px;
    height: 30px;
    margin-block-start: 0;
    margin-block-end: 0;
  }
  #attribute span{
    width: calc((100% - 10px) / 3);
    height: 30px;
    display: flex;
    flex-direction: row;
    margin-bottom: 10px;
  }
  #non_mob{
    position: relative;
    top: -20px;
    left: 20px;
    width: 280px;
  }
  #anomaly_icon{
    position: absolute;
    top: 60px;
  }